@import "/src/styles/variables.scss";

html,
body,
#root,
#root > div {
    height: 100%;

    @media #{$medium} {
        height: auto;
    }
}

html {
    height: 100vh;
    box-sizing: border-box;
    font-size: 16px;
    font-family: "Inter", sans-serif;

    @media #{$small} {
        font-size: 14px;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

body {
    min-width: 360px;
    background-color: var(--grey-light);
    line-height: 1.4;
    color: var(--font-clr);
}

button {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
}

button:hover {
    color: var(--secondary);
    cursor: pointer;
}

header {
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    height: 100px;
    color: white;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    color: var(--secondary);
}

h1 {
    font-size: 3em;
}

h2 {
    font-size: 2em;
}

h3 {
    font-size: 1.5em;
}

h4 {
    font-size: 1.2em;
}

p {
    color: var(--font-clr-light);
}
